import { Component, OnInit, ViewChild, TemplateRef } from '@angular/core';

import { TemplatePortal } from '@angular/cdk/portal';
import { SidebarContainerService } from 'src/app/core/components/sidebar/sidebar-container.service';

/***********************************
*  Tela DashBoard
*  @Aplicativo Fiscalizao WEB
*  @Autor willian.maruno
***********************************/
@Component({
  selector: 'app-dashboard-page',
  templateUrl: './dashboard-page.html',
  styleUrls: ['./dashboard-page.scss']
})
export class DashboardPage implements OnInit {
 

  @ViewChild('notificacoesSidebar') notificacoesSidebar: TemplateRef<any>;
  /***************************************************************************************************
   *            Construtor de pobre
   ***************************************************************************************************/
  constructor(private sidebarService: SidebarContainerService) {

  } 

  /***************************************************************************************************
   * Inicializa o componente
   ***************************************************************************************************/
  ngOnInit() { 
    this.sidebarService.open(new TemplatePortal(this.notificacoesSidebar, undefined, {}));
  }
  
}
